<template>
  <div id="app">
    <el-container>
    <el-header>
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" router>
        <el-menu-item index=""><img src="./assets/logo.png" width=80px height=60px  /></el-menu-item>
        <el-menu-item index="/" :disabled=!authenticated>Sites</el-menu-item>
        <el-menu-item index="/vendor" :disabled=!authenticated>Vendor</el-menu-item>
        <el-menu-item index="/pic" :disabled=!authenticated>Picture</el-menu-item>
        <el-submenu index="" v-if=authenticated>
          <template slot="title">hi, {{user}}</template>
          <el-menu-item index="" @click=handleLogout>logout</el-menu-item>
        </el-submenu>
        <el-menu-item v-else index="/login">Login</el-menu-item>
      </el-menu>
    </el-header>
    <el-main ><router-view/></el-main>
    <el-footer>
      &copy; Montage Lz Inc.
    </el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "App",
  created () {
      this.checkLogin()  
      // this.$store.state.authenticated = true
  },
  data() {
    return {
      activeIndex: "/",
    }
  },
  computed: {
    user: function() {
      return this.$store.state.user
    },
    authenticated: function() {
      if(this.$store.state.authenticated) {
          this.activeIndex = this.$route.path
      }
      return this.$store.state.authenticated
    },
  },
  methods:{
    checkLogin() {
      this.$store.dispatch('checkLogin').then(a => {
        if (a) {
          this.activeIndex = this.$route.path
        }
        else {
          this.activeIndex = '/login'
          this.$router.push(this.activeIndex);
        }
      }, e => {
        console.log(e)
      })   
    },
    handleLogout() {
      $.post('/adm/logout' , function() {
        console.log('ok')
      })
      this.$router.push('/login');
      this.$store.commit('setAuthenticated', null)
    }
  }
};
</script>

<style>
/* #app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
} */
</style>



