import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Vendor from '@/components/Vendor'
import Login from '@/components/Login'
import Picture from '@/components/Picture'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/vendor',
      name: 'Vendor',
      component: Vendor
    },
    {
      path: '/pic',
      name: 'Picture',
      component: Picture
    },   
    {
      path: '/login',
      name: 'Login',
      component: Login
    }
  ]
})



