<template>
  <div style="width: 80%; margin: 0 auto">
    <el-button type="" icon="el-icon-circle-plus" @click="newSite"
      >Add</el-button
    >
    <el-button type="" icon="el-icon-refresh" @click="fetchSites"
      >Refresh</el-button
    >

    <el-table :data="tableData">
      <el-table-column label="">
        <template slot-scope="scope">
          <img
            :src="scope.row.main_logo"
            width="80px"
            height="60px"
            :id="scope.$index"
          />
        </template>
      </el-table-column>

      <el-table-column label="Name">
        <template slot-scope="scope">
          <el-input
            v-model="scope.row.name"
            placeholder="Input a name"
          ></el-input>
        </template>
      </el-table-column>
      <el-table-column label="CreatedAt" width="200">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.date }}</span>
        </template>
      </el-table-column>
      <el-table-column label="State">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.valid"> </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="Update logo">
        <template slot-scope="scope">
          <el-upload
            class="upload-demo"
            :data="scope.row"
            :before-upload="beforeUpload"
            :on-success="handleUpSuccess"
            name="logo"
            :limit="2"
            action="/adm/site/upload"
          >
            <el-button
              slot="trigger"
              class="el-icon-circle-plus"
              plain
              size="medium"
              @click="handleSelect(scope.$index)"
              >click</el-button
            >
          </el-upload>
        </template>
      </el-table-column>

      <el-table-column label="Update bin" width="400">
        <template slot-scope="scope">
          <el-row :gutter="5">
            <el-col :span="50">
              <template>
                <el-input-number
                  placeholder="version"
                  v-model="scope.row.ver"
                  :min="1"
                  :precision="1"
                  :step="0.1"
                  size="small"
                  controls-position="right"
                ></el-input-number>
              </template>
            </el-col>

            <el-col :span="50">
              <el-upload
                class="upload-demo"
                :ref="scope.row.name"
                :before-upload="beforeUpload"
                :data="scope.row"
                name="bin"
                :limit="10"
                multiple
                action="/adm/site/upload"
                :auto-upload="false"
              >
                <el-button
                  slot="trigger"
                  class="el-icon-circle-plus"
                  plain
                  size="medium"
                  >click</el-button
                >
              </el-upload>
            </el-col>
          </el-row>
        </template>
      </el-table-column>
      <el-table-column label="Action">
        <template slot-scope="scope">
          <el-button
            type="success"
            icon="el-icon-check"
            circle
            @click="handleEdit(scope.$index, scope.row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            @click="handleRemove(scope.$index)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "HelloWorld",
  mounted() {
    this.fetchSites();
    // this.$store.dispatch("fetchVendor");
  },
  data() {
    return {
      currentIndex: 0,
    };
  },
  computed: {
    tableData: function () {
      return this.$store.state.sites;
    },
  },
  methods: {
    beforeUpload(file) {
      const isLimit = file.size / 1024 / 1024 < 20;
      if (!isLimit) {
        this.$message.error("File site beyond 20M!");
      }
    },
    handleSelect(i) {
      this.currentIndex = i;
    },
    handleUpSuccess(file) {
      var s = this.tableData[this.currentIndex].main_logo.split("?");
      this.tableData[this.currentIndex].main_logo =
        s[0] + "?" + new Date().getTime();
    },
    handleRemove(index) {
      this.$confirm("Are you sure to delete this site?", "Message", {
        confirmButtonText: "OK",
        cancelButtonText: "Cancel",
        type: "warning",
      })
        .then(() => {
          this.$store.dispatch("deleteSite", index).then(
            () => {
              this.$message.success("Delete success!");
            },
            (e) => {
              this.$message.warning("Delete failed!");
              console.log(e);
            }
          );
        })
        .catch(() => {});
    },
    handleEdit(index, site) {
      if (!site.name) {
        return this.$message.warning("Empty name!");
      }
      this.$store
        .dispatch("editSite", index)
        .then(
          (data) => {
            console.log("ok", data);
            this.$message.success("Edit success!");
          },
          (e) => {
            console.log("wwwww");
            if (e) {
              this.$message.warning("Edit failed!");
              console.log(e);
            }
          }
        )
        .finally(() => {
          this.$refs[site.name].submit();
        });
    },
    ...mapActions(["newSite", "fetchSites"]),
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



