<template>
<el-form :model="ruleForm" status-icon ref="ruleForm" label-width="100px"  style="width:500px; margin:0 auto; margin-top:30px">

  <el-form-item label="User" prop="username" :rules=rule>
    <el-input type="text" v-model="ruleForm.username" autocomplete="off"></el-input>
  </el-form-item>

  <el-form-item label="Password" prop="password" :rules=rule>
    <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
  </el-form-item>

  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')" >Sign</el-button>
    <el-button @click="resetForm('ruleForm')">Reset</el-button>
  </el-form-item>
</el-form>

</template>

<script>
  export default {
    name: "Login",
    data() {
      return {
        ruleForm: {
          username: '',
          password: '',
        },
        rule: [{ required: true, message: 'Empty input', trigger: 'blur' },]
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate(valid =>  {
          if (!valid) {
            return false
          } 
          
          $.ajax({
            type: 'POST',
            url: '/adm/login',
            data: this.ruleForm,
            dataType: 'json',
            success: r => {
              if(r.ok) {
                this.$message.success('Login success!')
                this.$store.commit('setAuthenticated', r.user)
                this.$router.push('/');
              }
              else {
                this.$message.warning(r.error)
              }
            },
            error: e => this.$message.warning('Login Fail!')
          });

        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>


