<template>
  <div style="width: 60%; margin: 0 auto">
    <el-button type="" icon="el-icon-circle-plus" @click="addVendor"
      >Add</el-button
    >
    <el-button type="" icon="el-icon-refresh" @click="fetchVendor"
      >Refresh</el-button
    >
    <el-table :data="tableData">
      <el-table-column label="Name">
        <template slot-scope="scope">
          <el-input
            v-model="scope.row.name"
            placeholder="Input a name"
          ></el-input>
        </template>
      </el-table-column>

      <!-- <el-table-column
      label="Auth sites"
      width=600>
      <template slot-scope="scope" >
        <el-button type="success" icon="el-icon-edit" circle @click="handleShow(scope.row)"></el-button>
        <el-transfer :titles=title v-model="scope.row.sites" :data="copySite" v-show="ok"></el-transfer>
      </template>
    </el-table-column> -->

      <el-table-column label="Update sites" width="540">
        <template slot-scope="scope">
          <el-collapse accordion>
            <el-collapse-item>
              <template slot="title">
                <i class="el-icon-edit"></i>
              </template>
              <template>
                <el-transfer
                  :titles="title"
                  v-model="scope.row.sites"
                  :data="copySite"
                ></el-transfer>
              </template>
            </el-collapse-item>
          </el-collapse>
        </template>
      </el-table-column>

      <el-table-column label="Action">
        <template slot-scope="scope">
          <el-button
            type="success"
            icon="el-icon-check"
            circle
            @click="handleEdit(scope.row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            @click="handleRemove(scope.$index)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "Vendor",
  mounted() {
    this.fetchVendor();
  },
  data() {
    return {
      title: ["not has", "has"],
    };
  },
  computed: {
    copySite: function () {
      var sites = this.$store.state.sites;
      if (sites)
        return sites.filter((i) => {
          if (i._id) {
            i.key = i._id;
            i.label = i.name;
            return i;
          }
        });
    },
    tableData: function () {
      var v = this.$store.state.vendor;
      // return v
      return (
        v &&
        v.map((i) => {
          i.date =
            i.createdAt && new Date(Date.parse(i.createdAt)).toLocaleString();
          i.old_name = i.name;
          i.show = false;
          return i;
        })
      );
    },
  },
  methods: {
    handleShow(row) {
      this.ok = !this.ok;
      row.show = !row.show;
    },
    addVendor() {
      this.tableData.push({ name: "", old_name: "", sites: [] });
    },
    handleRemove(index) {
      const { _id } = this.tableData[index];
      if (!_id) {
        return;
      }
      this.$confirm("Are you sure to delete this vendor?", "Message", {
        confirmButtonText: "OK",
        cancelButtonText: "Cancel",
        type: "warning",
      })
        .then(() => {
          this.tableData.splice(index, 1);
          $.ajax({
            type: "POST",
            url: "/adm/vendor/del?_id=" + _id,
            success: () => this.$message.success("Delete success!"),
            error: (e) => this.$message.warning("Delete failed!"),
          });
        })
        .catch(() => {});
    },
    handleEdit(row) {
      if (!row.name) {
        return this.$message.warning("Empty name!");
      }
      if (!row._id && this.tableData.find((n) => row.name === n.old_name)) {
        return this.$message.warning("Vendor name repeat!");
      }

      $.ajax({
        type: "POST",
        url: "/adm/vendor/edit",
        data: JSON.stringify(row),
        contentType: "application/json",
        dataType: "json",
        success: (r) => {
          Object.assign(row, r);
          this.$message.success("Edit success!");
        },
        error: (e) => this.$message.warning("Delete failed!"),
      });
    },
    ...mapActions(["fetchVendor"]),
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
</style>



