<template>
  <el-container>
    <el-aside style="width:276px">
      <el-input placeholder="Add a folder" v-model="input_name" clearable>
        <el-button
          slot="append"
          icon="el-icon-check"
          @click="
            Folderlist.push(input_name);
            input_name = null;
          "
        ></el-button>
      </el-input>

      <el-menu default-active="0" class="el-menu-demo" @select="handleSelect">
        <el-menu-item
          icon="el-icon-check"
          :index="index.toString()"
          v-for="(item, index) in Folderlist"
          :key="index"
          >{{ item }}
          </el-menu-item
        >
       
      </el-menu>
    </el-aside>
    <el-container>
      <el-main>
        <div>
          <el-upload
            :before-upload="beforeUpload"
            :on-err="handleUpError"
            name="picture"
            :limit="50"
            :ref="curApp"
            multiple
            :action="uploadAddr"
            style="width:500px"
          >
            <el-button slot="trigger" class="el-icon-upload" plain
              >Upload</el-button
            >
          </el-upload>
       
        </div>

        <div >
          <el-divider></el-divider>
          <el-button type="" icon="el-icon-refresh" @click="fetchPic"
            >Refresh</el-button
          >

          <el-button type="" icon="el-icon-edit" @click="onDeleteClick" v-show="totalCount">{{
            dodelete ? "Cancel" : "Delete"
          }}</el-button>

          <el-button
            type=""
            icon="el-icon-check"
            @click="postDelete"
            v-show="deletePics.length > 0"
            >Done</el-button
          >
        </div>


        <div style="margin-top:16px"> 
          <el-row :gutter="20">
           
            <el-col
              :span="4"
              v-for="(item, index) in displayCount"
              :key="index"
            >
            <el-tooltip :content="pics[index].name" placement="bottom" effect="light">
              <div
                class="grid-text"
                v-bind:class="{
                  'noraml-content': dodelete,
                  'choose-content': dodelete && pics[index].delete,
                }"
                @click="onChoose(pics[index], index)"
              >
                <el-image
                  style="width: 160px; height: 160px"
                  :src="pics[index].url"
                ></el-image>
               
                <div class="grid-name"> 
                  <span >{{ pics[index].name }}</span>
                </div>
                
              </div>
                </el-tooltip>
            </el-col>
          
          </el-row>
        </div>

        <el-divider>
          <el-button
            type=""
            icon="el-icon-refresh"
            @click="onLoad"
            v-show="displayCount < totalCount"
            >Load More</el-button
          >{{ displayCount >= totalCount ? "No more data." : "" }}</el-divider
        >
      </el-main>
    </el-container>
  </el-container>
</template>


<script>
const DISPLAY_COUNT_EACH = 18;

export default {
  name: "Picture",
  mounted() {
    $.getJSON(`/adm/pic/folders`, (r) => {
      if (!r) return;
      this.Folderlist = r;
      this.fetchPic();
    });
  },
  data() {
    return {
      currentIndex: "0",
      uploadList: null,
      deletePics: [],
      dodelete: false,
      Folderlist: [],
      pics: null,
      displayCount: 0,
      input_name: "",
    };
  },
  computed: {
    curApp: function () {
      return this.Folderlist[this.currentIndex];
    },
    uploadAddr: function () {
      return "/adm/pic/upload?app=" + this.curApp;
    },

    totalCount: function () {
      return this.pics ? this.pics.length : 0;
    },
  },
  methods: {
    handleSelect(i) {
      this.currentIndex = i;
      this.fetchPic();
    },

    fetchPic() {
      $.getJSON(`/api/pic?app=${this.curApp}`, (r) => {
        if (!r) return;
        this.pics = r.map((e) => {
          e.delete = false;
          return e;
        });

        this.displayCount =
          this.totalCount > DISPLAY_COUNT_EACH
            ? DISPLAY_COUNT_EACH
            : this.totalCount;

        this.$refs[this.curApp].clearFiles();
      });
    },
    onLoad() {
      if (this.displayCount + DISPLAY_COUNT_EACH > this.totalCount) {
        this.displayCount = this.totalCount;
      } else {
        this.displayCount += DISPLAY_COUNT_EACH;
      }
    },
    onDeleteClick() {
      this.dodelete = !this.dodelete;

      if (!this.dodelete) {
        this.deletePics.forEach((f) => {
          this.pics[f.id].delete = false;
        });
        this.deletePics.length = 0;
      }
    },

    onChoose(data, i) {
      if (this.dodelete) {
        data.delete = !data.delete;

        if (data.delete) {
          this.deletePics.push({ name: data.name, id: i });
        } else {
          this.deletePics = this.deletePics.filter((item) => item.id !== i);
        }
      }
    },
    postDelete() {
      this.$confirm("Are you sure to delete?", "Message", {
        confirmButtonText: "OK",
        cancelButtonText: "Cancel",
        type: "warning",
      })
        .then(() => {
          $.ajax({
            type: "POST",
            url: `/adm/pic/del?app=${this.curApp}`,
            data: JSON.stringify(this.deletePics),
            contentType: "application/json; charset=utf-8",
            dataType: "json",
            success: (r) => {
              this.fetchPic();
              this.dodelete = false;
              this.deletePics.length = 0;
            },
            error: (r) => {
              this.$message({
                type: "success",
                message: r,
              });
            },
          });
        })
        .catch(() => {});
    },
    beforeUpload(file) {
      const isLimit = file.size / 1024 / 1024 < 1;
      if (!isLimit) {
        this.$message.error("File site beyond 1000KB!");
        return false;
      }
    },

    handleUpError(e) {
      this.$message.error(e);
    },

    handleRemove(index) {
      this.$confirm("Are you sure to delete this site?", "Message", {
        confirmButtonText: "OK",
        cancelButtonText: "Cancel",
        type: "warning",
      })
        .then(() => {
          this.$store.dispatch("deleteSite", index).then(
            () => {
              this.$message.success("Delete success!");
            },
            (e) => {
              this.$message.warning("Delete failed!");
            }
          );
        })
        .catch(() => {});
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}

.grid-name {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.grid-text {
  text-align: center;
  margin-bottom: 16px;
}

.noraml-content {
  padding: 5px;
  padding-top: 16px;
  border-radius: 4px;
  border: 1px solid rgb(7, 117, 22);
  border-radius: 4px;
}

.choose-content {
  padding: 5px;
  padding-top: 16px;
  border-radius: 4px;
  box-shadow: 0 2px 4px rgb(111, 12, 6), 0 0 6px rgb(238, 5, 5);
}
</style>



