import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import { getJSON, ajax } from 'jquery'

export default new Vuex.Store({
  state: {
    token: '',
    sites: [],
    raw_sites: null,
    vendor: null,
    authenticated: false,
    user: null
  },
  mutations: {
    setToken (state, token) {
      state.token = token
      localStorage.setItem('token', token)
    },
    setSites (state, sites) {
      state.sites = sites.map(i => {
        i.date = new Date(Date.parse(i.createdAt)).toLocaleString()
        return i
      })
      // state.raw_sites = [...sites]
      state.raw_sites = [].concat(JSON.parse(JSON.stringify(sites)))
    }, 
    setAuthenticated (state, user) {
      state.user = user
      state.authenticated = user ? true: false
    }
  },

  actions: {
    fetchSites ({commit}) {
      getJSON('/adm/site', r => {
        if(!r) return 
        commit('setSites', r)
      })
    },
    fetchVendor ({state}) {
      getJSON('/adm/vendor', r => {
        if(!r) return
        state.vendor = r
      })
    },   
    newSite ({state}) {
      state.sites.push({valid: false, name: "", date:"", ver: 1})
    },
    editSite ({state}, index) {
      var site = state.sites[index]
      var raw = state.raw_sites[index]
      var post = {_id: site._id}
      return new Promise((resolve, reject) => {
        if(raw) {
          if(raw.name !== site.name) {
            post.origin_name =  raw.name
          }
          else if(raw.valid == site.valid) {
            return reject()
          }
        } 
        else if(state.raw_sites.find(n => site.name === n.name)) {
          return reject('Name repeat')
        }
        post.name = site.name
        post.valid = site.valid

        ajax({
          type: 'POST',
          url: '/adm/site/edit',
          data: post,
          dataType: 'json',
          success: r => {
            Object.assign(site, r)
            if(raw) {
              Object.assign(raw, r)
            }
            else {
              state.raw_sites.push(r)
            }
            site.date = new Date(Date.parse(site.createdAt)).toLocaleString()
            resolve(r)
          },
          error: reject
        });
      })
     
    },
    deleteSite ({state}, index) {
      return new Promise((resolve, reject) => {
        const {_id} = state.sites[index]
        state.sites.splice(index, 1)
        if(!_id) {
          return resolve()
        }
        state.raw_sites.splice(index, 1)
        ajax({
          type: 'POST',
          url: '/adm/site/del?_id='+_id,
          success: resolve,
          error: reject
        });  
      })
    },
    checkLogin ({commit, state}) {
      return new Promise((resolve, reject) => {
        if(process.env.NODE_ENV == "development") {
          commit('setAuthenticated', "developer")
          return resolve(state.user)
        }

        ajax({
          type: 'GET',
          url: '/adm/profile',
          dataType: 'json',
          success: r => {
            commit('setAuthenticated', r.user)
            resolve(r.user)
          },
          error: e => {
            commit('setAuthenticated', null)
            reject()
          }
        });  
      })
    }
  }
})



